import { Link } from "react-router-dom";
import {useState} from 'react';
import { MenuIcon,XCircleIcon } from '@heroicons/react/solid'
import Logo from '../../assets/logo.png';

const NavBar = () => {
    const [navbarOpen, setNavbarOpen] = useState(false);

  return (
    <>
      <nav className="relative flex flex-wrap items-center justify-between px-2  bg-[#3A3965] ">
        <div className="container px-4 mx-auto flex flex-wrap items-center justify-between">
          <div className="w-full relative flex justify-between lg:w-auto lg:static lg:block lg:justify-start">
            <a
              className="text-sm font-bold leading-relaxed inline-block mr-4 py-2 whitespace-nowrap uppercase text-white"
              href="/"
            >
               <img src={Logo} alt="React Logo"  className="w-[45%] md:w-[10%] animate__animated animate__pulse animate__infinite"/>
            </a>
            <button
              className="text-white cursor-pointer text-xl leading-none px-3 py-1 bg-white-500 border border-solid border-transparent rounded bg-transparent block lg:hidden outline-none focus:outline-none"
              type="button"
              onClick={() => setNavbarOpen(!navbarOpen)}
            >   
                {navbarOpen ? <XCircleIcon className="h-5 w-5 text-white-500"/> : <MenuIcon className="h-5 w-5 text-white-500"/>}
                 

            </button>
          </div>
          <div
            className={
              "lg:flex flex-grow items-center" +
              (navbarOpen ? " flex" : " hidden")
            }
            id="example-navbar-danger"
          >
            <ul className="flex flex-col lg:flex-row list-none lg:ml-auto">
              <li className="nav-item">
                <Link className="px-3 py-2 flex items-center text-xs uppercase font-bold leading-snug text-white hover:opacity-75" to="/">Inicio</Link>
              </li>
              <li className="nav-item">
                <Link className="px-3 py-2 flex items-center text-xs uppercase font-bold leading-snug text-white hover:opacity-75" to="routes">Rutas</Link>
              </li>
              <li className="nav-item">
                <Link className="px-3 py-2 flex items-center text-xs uppercase font-bold leading-snug text-white hover:opacity-75" to="About">Informacion</Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  )
}

export default NavBar