import React from 'react'
import { Loader } from '@googlemaps/js-api-loader';
import  './styles.css';
import CardDestino from '../../components/CardDestinos'
import MapRoute from '../../components/MapRoute';
const index = () => {

    const loader = new Loader({
        apiKey: "AIzaSyCb9RPMKKb_PYlSqXpLibKT55a0pp7TxfU",
        version: "weekly",
        libraries: ["places"]
    });



    loader
        .load()
        .then((google) => {
            const directionsService = new google.maps.DirectionsService();
            const directionsRenderer = new google.maps.DirectionsRenderer();
            const mapOptions = {
                zoom: 7,
                center: { lat: 41.85, lng: -87.65 },
            };
            const map = new google.maps.Map(document.getElementById("map"), mapOptions);

            directionsRenderer.setMap(map);

           
            calculateAndDisplayRoute(directionsService, directionsRenderer);
            


            function calculateAndDisplayRoute(directionsService, directionsRenderer) {
                directionsService
                  .route({
                    origin: {
                      lat:14.02754573793298, lng:-89.29879797471182
                    },
                    destination: {
                        lat:13.812960642198222, lng:-88.09183494592635
                    },
                    travelMode: google.maps.TravelMode.DRIVING,
                  })
                  .then((response) => {
                    directionsRenderer.setDirections(response);
                  })
                  .catch((e) => window.alert("Directions request failed due to " ));
              }
        })
        .catch(e => {
            // do something
        });
    return (
        <div className="grid sm:grid-cols-1 md:grid-cols-6 ">
            <div className='bg-slate-800'>
                <p className='text-white text-2xl font-bold text-center'>Guia de camino</p>
                asdsad
            </div>
            <div className='sm:col-span-4' id='mapa'>
               <MapRoute/>
            </div>
            <div className='sm:col-span-1 p-2'>
                <CardDestino/>
                <CardDestino/>
                <CardDestino/>
            </div>
        </div>
    )
}

export default index