import React from 'react'
import { Loader } from '@googlemaps/js-api-loader';
import { useEffect,useState } from 'react'
import fetch from '../../utils/fetch';
import './styles.css'
import { API_URL } from '../../utils/constans';

const ZonaCoberturaMap = ({id}) => {
  const [zonaCobertura,setZonaCobertura]=useState([]);
  useEffect(() => {
   
    fetch.fetchGet(`${API_URL}getZonaCobertura/${id}`).then(res=>setZonaCobertura(res));

  }, []);
  const loader = new Loader({
    apiKey: "AIzaSyCb9RPMKKb_PYlSqXpLibKT55a0pp7TxfU",
    version: "weekly",
    libraries: ["places"]
  });



  loader
    .load()
    .then((google) => {
      

      let puntos = zonaCobertura.puntos.map((item,index)=>{
        return JSON.parse(item.point);
      })
      console.log(puntos)
      const mapOptions = {
        zoom: 10,
        center: puntos[0],
      };
    
      const map = new google.maps.Map(document.getElementById("map"), mapOptions);
      const triangleCoords = puntos;
      // Construct the polygon.
      const bermudaTriangle = new google.maps.Polygon({
        paths: triangleCoords,
        strokeColor: "#FF0000",
        strokeOpacity: 0.8,
        strokeWeight: 2,
        fillColor: "#FF0000",
        fillOpacity: 0.35,
      });
    
      bermudaTriangle.setMap(map);
    })
    .catch(e => {
      // do something
    });

  return (
    <>
     {zonaCobertura && <div id="map"></div>}
    </>

  )
}

export default ZonaCoberturaMap