// dependecies
import { useState, useEffect } from 'react';
// css
import './styles.css'
// resources
import Sv from '../../assets/mapa.png';
// components
import { SearchIcon } from '@heroicons/react/solid';
import Spinner from '../../components/Spinner';
import Loading from '../../components/Loading';
import Departamento from '../../components/CardDepartamentList';
// utils
import { API_URL } from '../../utils/constans';
import fetch from '../../utils/fetch';


const Home = () => {
  const [loading, setLoading] = useState(true);
  const [departamentos, setDepartamentos] = useState([]);
  const [loadingSearch, setLoadingSearch] = useState(null);


  useEffect(() => {
    getDepartamentos();
  }, []);

  const getDepartamentos = async () => {
     setLoadingSearch(true)
    await fetch.fetchGet(`${API_URL}getDepartamentos`)
      .then(res => {
        setDepartamentos(res)
        setLoading(false)
        setLoadingSearch(false)
      });
  }

  const  buscarDepartamentos =async(data)=>{
    console.log(data.length)
    if(data.length == 0){
      getDepartamentos();
    }else{
      setLoadingSearch(true);
      await fetch.fetchGet(`${API_URL}getDepartamentos/${data}`)
      .then(res => {
        setDepartamentos(res)
        setLoading(false)
        setLoadingSearch(false)
      });
      
    }
  
    
  }

  if (loading) return <div className='flex items-center  justify-center  h-[100vh] '><Spinner /></div>
  return (
    <div className="grid sm:grid-cols-1 md:grid-cols-2 gap-4">

      <div className='p-5'>
        <div className='bg-[#001926] p-5 flex justify-center'>
          <input className='shadow appearance-none border  w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline' 
          placeholder='Buscar departamento...' type='text' onChange={(e)=>buscarDepartamentos(e.target.value)}/>
          <button className='bg-[#0B6CE2] px-2'>
            <SearchIcon className="h-5 w-5 text-white" />
          </button>
        </div>
        <div className='bg-white flex justify-center p-3 border-b-[2px] border-slate-200'>
          <p className='text-sm'>Para ver tiempos, horarios y paradas actualizados de Autobús por favor haga click abajo en un departamento</p>
        </div>

        {!loadingSearch ?
          departamentos.map((item, index) => {
            return <Departamento data={item} key={index.toString()} />
          })
          :
          <div className='flex items-center  justify-center  h-[20vh] '><Loading /></div>
        }
    
        
      </div>

      <div className='p-5'>
        <div className='flex items-center  justify-center border-1 rounded-lg shadow-xl bg-[#CBD4E1]'>
          <img src={Sv} alt="React Logo" className="w-[80%]" />
        </div>
        <div className='mt-5'>
          <p className='font-bold text-slate-700 mt-2 mb-5 text-2xl'>Voy App Web</p>
          <div className='p-7 bg-white shadow-md border-1'>
            <p>Voy implementa una innovadora forma de conocimiento de los diferentes trayectos del transporte público en las principales rutas turísticas del país.</p>
          </div>
        </div>
        <div className='mt-5'>
          <p className='font-bold text-slate-700 mt-2 mb-5 text-2xl'>¿Ayuda Voy?</p>
          <div className='p-7 bg-white shadow-md border-1'>
            <p>Voy ayuda a las personas a simplificar su transporte diario e identifica las trayectorias de autobuses en las principales zonas turísticas.</p>
          </div>
        </div>
        <div className='mt-5'>
          <p className='font-bold text-slate-700 mt-2 mb-5 text-2xl'>¿Que Puedo Esperar De Voy?</p>
          <div className='p-7 bg-white shadow-md border-1'>
            <p>Voy me permite conocer con mayor facilidad la ruta que más me conviene a tomar para trasladarme a mis destinos turísticos favoritos.</p>
          </div>
        </div>
        <div className='mt-5'>
          <p className='font-bold text-slate-700 mt-2 mb-5 text-2xl'>Autobuses</p>
          <div className='p-7 bg-white shadow-md border-1'>
            <p>Los precios y costos de los diferentes tipos de autobuses pueden variar dependiendo la unidad del transporte colectivo, en VOY puedes identificar el corrido en las principales zonas turísticas y el precio de cada ruta de autobuses interdepartamental e interurbana en el país.</p>
          </div>
        </div>
        <div className='mt-5'>
          <p className='font-bold text-slate-700 mt-2 mb-5 text-2xl'>Destinos</p>
          <div className='p-7 bg-white shadow-md border-1'>
            <p>En VOY puedes conocer y como llegar a los destinos turísticos más populares por los turistas en El Salvador.</p>
          </div>
        </div>
        <div className='mt-5'>
          <p className='font-bold text-slate-700 mt-2 mb-5 text-2xl'>Nosotros</p>
          <div className='p-7 bg-white shadow-md border-1'>
            <p>En VOY queremos ser el referente en la identificación de autobuses en las principales rutas turísticas en el país.</p>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Home;