import React from 'react'
import { ArrowCircleRightIcon } from '@heroicons/react/solid'
import './styles.css'
const depa = ({data}) => {
  return (
    <a href={`routes/${data.id}`} className='bg-white flecha grid grid-cols-1 md:grid-cols-2 border-b-2 p-4'>
      <div className=''>
        <p className='font-bold	text-slate-700'>{data.nombre}</p>
      </div>
      <div className=' flex justify-end'>
        <ArrowCircleRightIcon className="h-7 w-7  text-[#49B6B6]"/>
      </div>
    </a>
  )
}

export default depa