import React from 'react'
import DirectionsBusIcon from '@mui/icons-material/DirectionsBus';

const CardRouteList = ({data}) => {

  return (
    <a href={`../route/${data.id}`} className='bg-white flecha grid grid-cols-1 md:grid-cols-2 border-b-2 p-4'>
      <div className=''>
        <p className='font-bold	text-slate-700'>{data.nombre}</p>
      </div>
      <div className=' flex justify-end'>
        <DirectionsBusIcon className="h-7 w-7  text-slate-600"/>
      </div>
    </a>
  )
}

export default CardRouteList