// dependecies
import { useState, useEffect } from 'react';
import { SearchIcon } from '@heroicons/react/solid';
import ZonaCobertura from '../../components/ZonaCoberturaMap/ZonaCoberturaMap';
import {  useParams } from 'react-router-dom';
import CardRouteList from '../../components/CardRouteList/CardRouteList';
import { API_URL } from '../../utils/constans';
import fetch from '../../utils/fetch';
const RoutesDepartament = () => {
  let { departamento } = useParams();
  const [routes, setRoutes] = useState([]);

  useEffect(() => {
    getRoutes(departamento);
  }, []);


  const getRoutes =async(departamento)=>{
   
    await fetch.fetchGet(`${API_URL}getRoutesDepartamento/${departamento}`)
      .then(res => {
        setRoutes(res)
    });
  }
  return (
    <div className="grid sm:grid-cols-1 md:grid-cols-2 gap-4">
      <div className='p-5'>
      <div className='bg-[#001926] p-5 flex justify-center'>
          <input className='shadow appearance-none border  w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline' 
          placeholder='Buscar tu destino...' type='text'/>
          <button className='bg-[#0B6CE2] px-2'>
            <SearchIcon className="h-5 w-5 text-white" />
          </button>
        </div>
        <div className='bg-white flex justify-center p-3 border-b-[2px] border-slate-200'>
          <p className='text-sm'>Para conocer recorridos y paradas por favor hacer click en cada ruta.</p>
        </div>
        {
          routes.map((item,index)=>{
            return <CardRouteList data={item}/>
          })
        }
      </div>

      <div className='p-5'>
        <ZonaCobertura id={departamento}/>
      </div>
    </div>
  )
}

export default RoutesDepartament